import { Component, OnInit, ViewChild, Input, Output, OnChanges, SimpleChange, EventEmitter, SimpleChanges } from '@angular/core';
import { isNullOrUndefined } from 'util';

declare var window: any;

@Component({
  selector: 'app-error-stack-window',
  templateUrl: './error-stack-window.component.html',
  styleUrls: ['./error-stack-window.component.css']
})
export class ErrorStackWindowComponent implements OnInit, OnChanges {
  @Input() triggerShowWindow: boolean;
  @Input() errorStack: string;
  @Input() errorStackHead: string;

  /**
   * 异常堆栈窗口是否显示
   */
  showErrorStackWindow = false;
  errorToken = '#GSPDBOError#';

  constructor() { }

  ngOnInit() {
  }

  ngOnChanges(changes: SimpleChanges): void {
    // 初次加载不显示
    if (isNullOrUndefined(changes.triggerShowWindow) || isNullOrUndefined(changes.triggerShowWindow.previousValue)) {
      return;
    }
    if (!this.showErrorStackWindow) {
      this.showModal();
    }
  }

  showModal(): void {
    this.showErrorStackWindow = true;
  }

  /**
   * 关闭弹窗
   */
  closeErrorStackWindow() {
    this.showErrorStackWindow = false;
  }

  /**
   * 复制异常堆栈信息
   */
  copyErrorStack() {
    const range = document.createRange();
    range.selectNode(document.getElementById('errorStack'));

    const selection = window.getSelection();
    if (selection.rangeCount > 0) {
      selection.removeAllRanges();
    }

    selection.addRange(range);
    document.execCommand('copy');
    alert('复制成功！');
  }
}



