import { Component, OnInit, OnDestroy, Input, OnChanges, SimpleChange, ViewChild } from '@angular/core';
import { Validators, FormBuilder, FormGroup } from '@angular/forms';
import { DatabaseObjectTable } from '../utils/lib/databaseobjecttable';
import { DataBindingDirective, GridComponent, SelectableSettings } from '@progress/kendo-angular-grid';
import { DropDownListComponent } from '@progress/kendo-angular-dropdowns';
import { GridDataResult, DataStateChangeEvent } from '@progress/kendo-angular-grid';
import { Observable } from 'rxjs/Observable';
import { isNullOrUndefined } from 'util';
import { List, Map, fromJS, is } from 'immutable';
import { DatabaseObjectTableView } from '../utils/lib/databaseobjecttableview';
import { DatabaseObjectIndex } from '../utils/lib/databaseobjectindex';
import { DatabaseObjectColumn } from '../utils/lib/databaseobjectcolumn';
import { DatabaseObjectIndexView } from '../utils/lib/databaseobjectindexview';
import { DatabaseObjectIndexViewList } from '../utils/lib/databaseobjectindexviewlist';
import { DatabaseObjectIndexConst } from '../const/databaseobjectindexconst';
import {TableIndexColumnGridComponent} from '../table-index-column-grid/table-index-column-grid.component';

@Component({
  selector: 'app-indexes-manager',
  templateUrl: './indexes-manager.component.html',
  styleUrls: ['./indexes-manager.component.css']
})
export class IndexesManagerComponent implements OnInit, OnChanges, OnDestroy {
  @Input() databaseObject: DatabaseObjectTable;
  @Input() currentView: DatabaseObjectTableView;
  @ViewChild('grid') grid: GridComponent;
  @ViewChild('tabelIndexColumnGrid') tabelIndexColumnGrid: TableIndexColumnGridComponent;
  currentIndex: DatabaseObjectIndex;
  currentIndexView: DatabaseObjectIndexView;
  indexes: DatabaseObjectIndex[];
  databaseObjectView: DatabaseObjectTableView;
  public selectedIndexes: any[] = [];
  subscriptions = new Array<any>();
  currentDatabaseObject: DatabaseObjectTable;

  constructor(private formBuilder: FormBuilder) { }

  ngOnInit() {
    this.databaseObjectView = this.currentView;
  }
  ngOnChanges(changes: { [propKey: string]: SimpleChange }) {
    if (changes['databaseObject']) {
      this.databaseObject = changes['databaseObject'].currentValue;
      this.indexes = this.databaseObject.indexes;
    }

    if (changes['currentView']) {
      this.closeGridCell();
      // 更新viewObject
      this.databaseObjectView = changes['currentView'].currentValue;
      this.refresh();
    }
    if (!isNullOrUndefined(this.indexes)) {
    this.currentIndex = this.indexes[0];
      this.currentIndexView = this.currentView.indexes.getItemByID(this.currentIndex .id) as DatabaseObjectIndexView;
  }

  }

  ngOnDestroy(): void {
    this.unsubscribeRefreshView();
  }

  closeGridCell() {
    // 关闭所有正在编辑中的单元格
    this.grid.closeCell();
    // 取消所有编辑中的列
    this.grid.cancelCell();
    // 取消新增行
    this.grid.closeRow(-1);
  }

  public dataStateChange(state: DataStateChangeEvent): void {

  }

  refresh() {
    if (isNullOrUndefined(this.databaseObjectView)) {
      return;
    }
    this.currentDatabaseObject = this.databaseObjectView.toJS();
    if (isNullOrUndefined(this.databaseObjectView.indexes) || this.databaseObjectView.indexes.length <= 0) {
      this.currentIndexView = null;
      this.indexes = null;
    } else {
      this.indexes = this.databaseObjectView.indexes.toJS();
    }
    this.subscribeRefreshView();
  }
  /**
       * 订阅视图刷新事件
       */
  private subscribeRefreshView() {
    this.unsubscribeRefreshView();
    if (isNullOrUndefined(this.databaseObjectView.indexes)) {
      this.currentIndex = null;
     return;
    }
    // 订阅当前关联数据源的值刷新事件
    // 每次刷新后，需重新绑定字段列表的事件
    const refreshViewSubscription = this.databaseObjectView.indexes.refreshView.subscribe(
      value => {
        if (isNullOrUndefined(this.databaseObjectView)) {
          return;
        }
        this.currentDatabaseObject = this.databaseObjectView.toJS();
        if (isNullOrUndefined(this.databaseObjectView.indexes) || this.databaseObjectView.indexes.length <= 0) {
          this.indexes = null;
          this.currentIndex = null;
          this.currentIndexView = null;
        }else {
          this.indexes = this.databaseObjectView.indexes.toJS();
          if (isNullOrUndefined(this.currentIndex)) {
            this.currentIndex = this.indexes[0];
          }
          this.currentIndexView = this.currentView.indexes.getItemByID(this.currentIndex.id) as DatabaseObjectIndexView;
        }
      }
    );
    this.subscriptions.push(refreshViewSubscription);
  }

  /**
   * 取消订阅视图刷新事件
   */
  private unsubscribeRefreshView() {
    while (this.subscriptions.length > 0) {
      const subscription = this.subscriptions.pop();
      subscription.unsubscribe();
    }
  }

  addIndex() {
    const newIndex = new DatabaseObjectIndex();
    this.addNewIndex(newIndex);
  }

  private addNewIndex(item: DatabaseObjectIndex) {
    if (isNullOrUndefined(this.databaseObjectView.indexes) || this.databaseObjectView.indexes.length <= 0) {
      this.databaseObjectView.indexes = new DatabaseObjectIndexViewList(new Array<DatabaseObjectIndex>());
    }
    this.databaseObjectView.indexes.add(new DatabaseObjectIndexView(Map(item)));
    this.refresh();
  }


  deleteIndex() {
    if (this.selectedIndexes.length === 0) { alert('请先选中要删除的字段。'); } else {
     // while (this.selectedIndexes.length !== 0) {
        const id = this.selectedIndexes.pop();
        this.databaseObjectView.indexes.removeByID(id);
     // }
    }
  }

  /**
     * 新增后的行按钮（确认）
     */
  public confirmHandler({ sender, formGroup, rowIndex, dataItem }) {
    if (formGroup.valid) {
      const ele = this.mergeFormGroupValue(formGroup);
      this.addNewIndex(ele);
      sender.closeRow(rowIndex);
    }
  }
  /**
   * 合并表格中的信息与默认新增信息
   * @param formGroup
   */
  private mergeFormGroupValue(formGroup: FormGroup): DatabaseObjectIndex {
    if (isNullOrUndefined(formGroup)) {
      alert('当前字段信息未定义。'); // 异常
    }
    const ele = formGroup.value['column'];
    ele.Name = formGroup.value['Name'];
    ele.ID = formGroup.value['ID'];
    ele.IsUnique = formGroup.value['IsUnique'];
    ele.IsCluster = formGroup.value['IsCluster'];
    ele.IfPrimaryKey = formGroup.value['IfPrimaryKey'];
    ele.Columns = formGroup.value['Columns'];
    return ele;
  }
  /**
   * 新增后的行按钮（取消）
   */
  public cancelAddNewItemHandler(args: any) {
    const { sender, rowIndex } = args;
    sender.closeRow(rowIndex);
  }

  cellClickHandler(args: any) {
    const { sender, rowIndex, columnIndex, column, dataItem, isEdited, isNew } = args;

    // 若为新增行，不响应
    if (rowIndex === -1) {
      return;
    }
    // 选中当前字段
    this.databaseObjectView.indexes.setSelectIndexByID(dataItem.id);
    this.selectChanged(dataItem);
    // ② 编辑属性
    if (!isEdited) {
      sender.editCell(rowIndex, columnIndex, this.createFormGroup(dataItem));
      return;
    }
  }
  private createFormGroup(index: any): FormGroup {
    return this.formBuilder.group({
      'code': [index.code],
      'name': [index.name],
      'isCluster': [index.isCluster],
      'isUnique': [index.isUnique],
      'ifPrimaryKey': [index.ifPrimaryKey],
      'id': [index.id], // 不显示，ID
      'index': [index], // 不显示，保留字段初始化信息
      'columns': [index.columns]
    });
  }

  cellCloseHandler(args: any) {
    const { sender, formGroup, dataItem, rowIndex, column } = args;

    if (!formGroup.valid) {
      // 若验证不通过，则编辑器不失焦
      args.preventDefault();
    } else if (formGroup.dirty) {
      const propertyName = column.field;
      const newValue = formGroup.value[propertyName];
      // 当前属性赋值
      this.changeCurrentProperty(propertyName, dataItem, newValue);
  }
}
    changeCurrentProperty(propertyName: string, dataItem: DatabaseObjectIndex, newValue: any): any {
      // ③ 允许修改属性
      this.updateValueByElementId(dataItem.id, propertyName, newValue);
    }
  /**
* 根据字段ID更新属性值
* @param elementId 字段ID
* @param propertyName 属性名
* @param propertyValue 属性值
*/
  updateValueByElementId(elementId: string, propertyName: string, propertyValue: any) {
    this.databaseObjectView.indexes.updateByID(elementId, elementView => {
      elementView.update(propertyName, oldValue => propertyValue);
      return elementView;
    });
  }
  removeColumn(args: any) {

  }
  addHandler(args: any) {

  }
  changeIsUniqueCheck(isUnique: boolean, index: DatabaseObjectIndex) {
    this.updateValueByElementId(index.id, DatabaseObjectIndexConst.IsUnique_Constraint, isUnique);
  }
  changeIsClusterCheck(isCluster: boolean, index: DatabaseObjectIndex) {
    if (!isCluster) {
      this.updateValueByElementId(index.id, DatabaseObjectIndexConst.IsCluster_Constraint, isCluster);
      return;
    }
    const indexes = this.currentView.indexes.toJS();
    let  isExistCluster = false;
    indexes.forEach( eccIndex => {
      if ( eccIndex.isCluster_Constraint) {
        isExistCluster = true;
    }});
    if ( isExistCluster) {
     alert('索引集合中已存在聚集索引，不允许当前索引添加聚集属性。');
    this.updateValueByElementId(index.id, DatabaseObjectIndexConst.IsCluster_Constraint, false);
    return;
    }
    this.updateValueByElementId(index.id, DatabaseObjectIndexConst.IsCluster_Constraint, isCluster);
  }
  changeIfPrimaryKeyCheck(ifPrimaryKey: boolean, index: DatabaseObjectIndex) {
    if (!ifPrimaryKey) {
      this.updateValueByElementId(index.id, DatabaseObjectIndexConst.IfPrimary_Key, ifPrimaryKey);
      return;
    }
    const indexes = this.currentView.indexes.toJS();
    let isExistPrimaryKeyIndex = false;
    indexes.forEach( eccIndex => {
      if ( eccIndex.ifPrimary_Key) {
        isExistPrimaryKeyIndex = true;
    }});
    if (isExistPrimaryKeyIndex) {
     alert('索引集合中已存在含有主键的索引，不允许当前索引添加主键。');
    this.updateValueByElementId(index.id, DatabaseObjectIndexConst.IfPrimary_Key, false);
    return;
    }
  this.updateValueByElementId(index.id, DatabaseObjectIndexConst.IfPrimary_Key, ifPrimaryKey);
}

  selectChanged(dataItem: DatabaseObjectIndex) {
    this.currentIndex = dataItem;
    this.currentIndexView = this.currentView.indexes.getItemByID(dataItem.id) as DatabaseObjectIndexView;
  }
  editIndexColumns() {
    if (this.selectedIndexes.length === 0) {
      alert('请先选中要编辑的索引字段。');
      return;
   } else {
      // while (this.selectedIndexes.length !== 0) {
    this.tabelIndexColumnGrid.editIndexColumns();
    }
  } }
  // }



