export enum DboType {
    /// <summary>
    /// 表
    /// </summary>
    Table = 0,

    /// <summary>
    /// 视图
    /// </summary>
    View = 1 ,

    /// <summary>
    /// 函数
    /// </summary>
    Function = 2,

    /// <summary>
    /// 存储过程
    /// </summary>
    StoreProcedure = 5
}

export const DboTypes: Array<{ index: number, text: string, value: DboType }> = [
    // { index: 0, text: '', value: DboType.Table}
    { index: 1, text: '视图(功能完善中)', value: DboType.View},
    { index: 2, text: '函数(功能完善中)', value: DboType.Function},
    { index: 5, text: '存储过程(功能完善中)', value: DboType.StoreProcedure }
];



