
export class DatabaseObjectDto {
    path: string;
    type: string;
    fileName: string;
    content: string;

    constructor(path: string, fileName: string, type: string, content: string) {
        this.path = path;
        this.fileName = fileName;
        this.type = type;
        this.content = content;
    }
}



