import { GSP, Package } from '@farris/ide-devkit';
import { ComponentFactoryResolver, Injector, Type } from '@angular/core';
import { DboCreatorComponent } from './dbo-creator/dbo-creator.component';
import { DboCreateWizardPackageView } from './dbo-creator/dbo-create-wizard-package-view';


export class DBOCreateWizardPackage extends Package {
    constructor(gsp: GSP, private cfr: ComponentFactoryResolver, private injector: Injector) {
        super(gsp);
    }

    activate(state) {
        this.gsp.eventBus.on(null, null, 'Command:newDbo', this, () => this.showModal());
    }

    deactivate() { }

    serialize() {
        return {
            FormCreateWizardPackageViewState: this.packageView.serialize()
        };
    }

    toggle() { }

    getComponent(): Type<any> {
        return DboCreatorComponent;
    }

    showModal() {
        const packageView = new DboCreateWizardPackageView({}, this.cfr, this.injector);
        const title = '新建数据库对象';
        const comp = packageView.getElement().instance as DboCreatorComponent;
        let isShow = true;
        this.gsp.eventBus.requestFor('FileTreePanelPackage', 'dboCreate', 'AccessFileTree', { action: '6' }, (nodeInfo) => {
            if (!nodeInfo) {
                alert('请先选择dbo存放目录');
                isShow = false;
                return;
            }
            if (!nodeInfo.nodePath) {
                alert('请先选择dbo存放目录');
                isShow = false;
                return;
            }
            if (nodeInfo.nodeType === 'folder' || nodeInfo.nodeType === 'project') {
                comp.path = nodeInfo.nodePath + '/';
            } else if (nodeInfo.nodeType === 'file') {
                const handledPath = nodeInfo.nodePath.replace(/\\/g, '/');
                comp.path = handledPath.substring(0, handledPath.lastIndexOf('/'));
            } else {
                console.log('unknowed type');
                alert('请选择合适的存放目录！');
                isShow = false;
                return;
            }
        });
        if (!isShow) {
            return;
        }
        if (comp) {
            comp.createDboSuccess.subscribe(() => { this.refreshTree(); });
            comp.createDboEvent.subscribe((data) => this.createDbo(data));
        }
        const modalPanel = this.gsp.workspace.addModalPanel({
            item: {
                element: packageView.getElement(),
                modalOptions: {
                    title: title,
                    width: 420,
                    height: 320,
                    buttons: comp.modalFooter,
                    showMaxButton: false
                }
            }
        });
        comp.modalClosed.subscribe(() => modalPanel.destroy());

    }
    private createDbo(data) {
        // this.gsp.eventBus.post(DBOCreateWizardPackage, this.id, 'Command:CreateMetadata', data);
        this.gsp.workspace.open(data);
    }
    refreshTree() {
        this.gsp.eventBus.requestFor('FileTreePanelPackage', 'dboCreate', 'AccessFileTree', { action: '5' }, () => {
            console.log('event has been deal done');
        });
    }

}





