import { Pipe, PipeTransform } from '@angular/core';
import { DatabaseObjectColumn } from '../utils/lib/databaseobjectcolumn';
import {GSPDBODataTypes} from '../utils/lib/datatype';
import { isNullOrUndefined } from 'util';

/**
 * 字段数据类型
 */
@Pipe({ name: 'gspDBODataTypeConvert' })
export class GSPDBODataTypeConvert implements PipeTransform {
  transform(column: DatabaseObjectColumn): string {
    const dataType = column.dataType;
    if (isNullOrUndefined(dataType)) { alert('dataType为空'); }
    return GSPDBODataTypes.find(item => item.value === dataType).text;
  }
  remove() {

  }
  addHandler() {

  }
}



