import { MapBase } from '../../base/mapbase';
import { List, Map, fromJS } from 'immutable';
import { isNullOrUndefined, isNull } from 'util';
import { DatabaseObjectColumn } from './databaseobjectcolumn';
import { Guid } from './guid';
import { DataType } from './datatype';

export class DatabaseObjectIndex {

    constructor() {
        this.id = Guid.newGuid();
        this.isUnique_Constraint = false;
        this.isCluster_Constraint = false;
        this.IfPrimary_Key = false;
        this.columns = new Array<DatabaseObjectColumn>();
    }
    id: string;
    name: string;
    isCluster_Constraint: boolean;
    isUnique_Constraint: boolean;
    IfPrimary_Key: boolean;
    columns: DatabaseObjectColumn[];
}



