import { Component, OnInit, OnChanges, SimpleChanges, OnDestroy, Input } from '@angular/core';
import { DatabaseObjectTable } from '../utils/lib/databaseobjecttable';
import { DatabaseObjectTableView } from '../utils/lib/databaseobjecttableview';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Subscription } from 'rxjs/Subscription';
import { isNullOrUndefined } from 'util';
// import { MessagerService } from '@farris/ui';
import { DatabaseObjectColumnConst } from '../const/databaseobjectcolumnconst';

@Component({
  selector: 'app-basic-info-card',
  templateUrl: './basic-info-card.component.html',
  styleUrls: ['./basic-info-card.component.css']
})
export class BasicInfoCardComponent implements OnInit, OnChanges, OnDestroy {
  @Input() databaseObject: DatabaseObjectTable;
  @Input() currentView: DatabaseObjectTableView;
  tempDatabseObject: DatabaseObjectTable;
  public formGroup: FormGroup;
  /**
    * viewAction视图层刷新事件订阅
    */
  private refreshSubscriptions = new Array<Subscription>();

  /**
   * 表单值更新事件订阅
   * 更改类型为Subscription时，报错：类型冲突
   */
  private formGroupSubscriptions: any[];

  constructor(private formBuilder: FormBuilder) { }

  ngOnInit() {
    this.load();
    this.tempDatabseObject = this.databaseObject;
  }

  ngOnChanges(changes: SimpleChanges): void {
    this.tempDatabseObject = this.databaseObject;
    this.refresh();
  }

  ngOnDestroy(): void {
    this.unsubscribeRefreshLayer();
  }

  /**
   * 加载
   */
  private load(): any {
    this.createForm();
    this.refresh();
  }

  /**
   * 刷新
   */
  private refresh() {
    if (isNullOrUndefined(this.currentView)) {
      this.clearFormValue();
      return;
    }
    this.unsubscribeRefreshLayer();
    this.databaseObject = this.currentView.toJS();
    this.setFormValue(this.databaseObject);
    this.subscribeRefreshViewLayer();
  }
  changeIsI18nObjectCheck(isMultiLanguage: boolean) {
    if (!isMultiLanguage && this.tempDatabseObject.isI18nObject) {
   alert('数据库对象设置为多语并保存后不允许再改成不是多语。');
      this.currentView.IsI18nObject = true;
   return;
  } else {
    const mulityLanguageColumns = this.currentView.columns.getI18nItem();
      if (!isMultiLanguage && !isNullOrUndefined(mulityLanguageColumns)) {
      // alert('存在字段设置为启用多语，请取消后将数据库对象设置为不启用');
      // this.messager.question('存在字段设置为启用多语，继续将使所有字段设为不启用多语，是否继续？',
      //                 () => this.okCallback(mulityLanguageColumns));
      this.okCallback(mulityLanguageColumns);
      return;
    } else {
      this.currentView.IsI18nObject = isMultiLanguage;
    }
  }
}
  okCallback(mulityLanguageColumns: Array<any>) {
    mulityLanguageColumns.forEach(item => {
      this.updateValueByElementId(item.get('id'), DatabaseObjectColumnConst.IsMultiLanguage, false);
    });
    this.currentView.IsI18nObject = false;
  }

  changeIsFiscalTableCheck(isFiscalTable: boolean) {
    if (!isFiscalTable && this.tempDatabseObject.isFiscalTable) {
      alert('数据库对象设置为年度表并保存后不允许再改成不是年度表。');
      this.currentView.IsFiscalTable = true;
      return;
    } else {
      this.currentView.IsFiscalTable = isFiscalTable;
      }
  }
  changeIsSynHisTableCheck(isSynHis: boolean) {
    if (!isSynHis && this.tempDatabseObject.isSynHis) {
      alert('数据库对象设置为同步历史表并保存后不允许再改成不同步历史表。');
      this.currentView.IsSynHis = true;
      return;
    } else {
      this.currentView.IsSynHis = isSynHis;
    }
  }
  changeIsUsingTimeStampCheck(isUsingTimeStamp: boolean) {
    if (!isUsingTimeStamp && this.tempDatabseObject.isUsingTimeStamp) {
      alert('数据库对象设置启用时间戳字段并保存后不允许再改成不启用。');
      this.currentView.IsUsingTimeStamp = true;
      return;
    } else {
      this.currentView.IsUsingTimeStamp = isUsingTimeStamp;
    }
  }
  /**
* 根据字段ID更新属性值
* @param elementId 字段ID
* @param propertyName 属性名
* @param propertyValue 属性值
*/
  updateValueByElementId(elementId: string, propertyName: string, propertyValue: any) {
    this.currentView.columns.updateByID(elementId, elementView => {
      elementView.update(propertyName, oldValue => propertyValue);
      return elementView;
    });
  }
  /**
   * 初始化表单
   */
  private createForm() {
    this.formGroup = this.formBuilder.group({
      code: ['', Validators.required],
      name: ['', Validators.required],
      isI18nObject: [false, Validators.required],
      description: ['', Validators.required],
      isFiscalTable: [false, Validators.required],
      isUsingTimeStamp: [false, Validators.required],
      isSynHis: [false, Validators.required],
    });
  }

  /**
   * 表单赋值
   * @param be
   */
  private setFormValue(databaseObject: DatabaseObjectTable) {
    this.unsubscribeFormGroupValueChanges();
    if (isNullOrUndefined(databaseObject)) {
      this.clearFormValue();
    }
    if (isNullOrUndefined(this.formGroup)) {
      this.createForm();
    }
    this.formGroup.enable();
    this.formGroup.patchValue({
      code: databaseObject.code || '',
      name: databaseObject.name || '',
      description: databaseObject.description || '',
      isI18nObject: databaseObject.isI18nObject || false,
      isFiscalTable: databaseObject.isFiscalTable || false,
      isUsingTimeStamp: databaseObject.isUsingTimeStamp || false,
      isSynHis: databaseObject.isSynHis || false
    });
    this.subscribeFormGroupValueChanges();
  }

  /**
   * 表单清空
   */
  private clearFormValue() {
    this.unsubscribeFormGroupValueChanges();
    if (isNullOrUndefined(this.formGroup)) {
      this.createForm();
    }
    this.formGroup.disable();
    this.formGroup.patchValue({
      code: '',
      name: '',
      description:  '',
      isI18nObject: false,
       isFiscalTable: false,
      isUsingTimeStamp: false,
      isSynHis: false
    });
    this.subscribeFormGroupValueChanges();
  }

  /**
   * 订阅表单值改变事件
   */
  private subscribeFormGroupValueChanges() {
    const subscriptionCode = this.formGroup.controls.code.valueChanges.subscribe(code => {
      this.currentView.Code = code;
    });
    const subscriptionName = this.formGroup.controls.name.valueChanges.subscribe(name => {
      this.currentView.Name = name;
    });
    const subscriptionIsFiscalTable = this.formGroup.controls.isFiscalTable.valueChanges.subscribe(isFiscalTable => {
      this.currentView.IsFiscalTable = isFiscalTable;
    });
    const subscriptionIsUsingTimeStamp = this.formGroup.controls.isUsingTimeStamp.valueChanges.subscribe(isUsingTimeStamp => {
      this.currentView.IsUsingTimeStamp = isUsingTimeStamp;
    });
    const subscriptionIsSynHis = this.formGroup.controls.isSynHis.valueChanges.subscribe(isSynHis => {
      this.currentView.IsSynHis = isSynHis;
    });
    const subscriptionIsI18nObject = this.formGroup.controls.isI18nObject.valueChanges.subscribe(isI18nObject => {
      this.currentView.IsI18nObject = isI18nObject;
    });
    const subscriptionDescription = this.formGroup.controls.description.valueChanges.subscribe(description => {
      this.currentView.Description = description;
    });
    if (isNullOrUndefined(this.formGroupSubscriptions)) {
      this.formGroupSubscriptions = new Array<any>();
    }
    this.formGroupSubscriptions.push(subscriptionCode);
    this.formGroupSubscriptions.push(subscriptionName);
    this.formGroupSubscriptions.push(subscriptionIsI18nObject);
    this.formGroupSubscriptions.push(subscriptionDescription);
    this.formGroupSubscriptions.push(subscriptionIsFiscalTable);
    this.formGroupSubscriptions.push(subscriptionIsUsingTimeStamp);
    this.formGroupSubscriptions.push(subscriptionIsSynHis);
  }

  /**
   * 订阅viewAction视图层刷新事件
   */
  subscribeRefreshViewLayer(): any {
    const subscription = this.currentView.refreshView.subscribe(value => {
      this.databaseObject = value.toJS();
      this.setFormValue(this.databaseObject);
    });
    this.refreshSubscriptions.push(subscription);
  }

  /**
   * 取消订阅viewAction视图层刷新事件
   */
  private unsubscribeRefreshLayer() {
    if (this.refreshSubscriptions) {
      while (this.refreshSubscriptions.length > 0) {
        const subscription = this.refreshSubscriptions.pop();
        subscription.unsubscribe();
      }
    }
  }

  /**
   * 取消订阅表单值改变事件
   */
  private unsubscribeFormGroupValueChanges() {
    if (this.formGroupSubscriptions && this.formGroupSubscriptions.length > 0) {
      while (this.formGroupSubscriptions.length > 0) {
        const subscription = this.formGroupSubscriptions.pop();
        subscription.unsubscribe();
      }
    }
  }
}



