import { Component, OnInit, ViewChild, Inject } from '@angular/core';
import { ColumnsManagerComponent } from '../columns-manager/columns-manager.component';
import { DatabaseObjectTable } from '../utils/lib/DatabaseObjectTable';
import { DatabaseObjectService } from '../database-object.service';
import { DatabaseObjectColumn } from '../utils/lib/databaseobjectcolumn';
import { DatabaseObjectTableView } from '../utils/lib/databaseobjecttableview';
import { IndexesManagerComponent } from '../indexes-manager/indexes-manager.component';
import { TableIndexColumnGridComponent } from '../table-index-column-grid/table-index-column-grid.component';
import { DatabaseObjectIndex } from '../utils/lib/databaseobjectindex';
import { isNullOrUndefined } from 'util';
import { BasicInfoCardComponent } from '../basic-info-card/basic-info-card.component';

@Component({
  selector: 'app-table-designer',
  templateUrl: './table-designer.component.html',
  styleUrls: ['./table-designer.component.css']
})
export class TableDesignerComponent implements OnInit {
  tabNameColumn = '字段集合';
  tabNameIndex = '索引集合';
  currentTabTitle = this.tabNameColumn;
  databaseObject: DatabaseObjectTable;
  currentView: DatabaseObjectTableView;
  currentIndex: DatabaseObjectIndex;
  fileUri: string;
  relativePath: string;
  fileName: string;
  /**
 * 异常堆栈窗口是否显示
 */
  showErrorStackWindow = false;
  errorStack: string;
  errorStackHead: string;
  errorToken = '#GSPDBOError#';
  savedErrorToken = '#saved#';
  @ViewChild('columnCollection') columnCollection: ColumnsManagerComponent;
  @ViewChild('indexCollection') indexCollection: IndexesManagerComponent;
  @ViewChild('basicInfoCard') basicInfoCard: BasicInfoCardComponent;
  constructor(private databaseObjectService: DatabaseObjectService) {

  }


  ngOnInit() {
    // 解析URL，获取relativePath和fileName
    this.resolveUrl();
    this.reloadDatabaseObject();
  }
  resolveUrl() {
    this.fileUri = this.fileUri.replace(/\\/g, '/');
    const s = this.fileUri.split('/');
    this.fileName = s[s.length - 1];
    this.relativePath = this.fileUri.substring(1, this.fileUri.lastIndexOf('/'));
  }
  saveDatabaseObject() {
    if (!isNullOrUndefined(this.columnCollection)) {
      this.columnCollection.closeGridCell();
    }
    if (!isNullOrUndefined(this.indexCollection)) {
      this.indexCollection.closeGridCell();
    }
    const table = this.currentView.toJS();
    this.databaseObjectService.saveDatabaseObject(this.relativePath, this.fileName, table).subscribe(response => {
      if (response.status === 200) {
        alert('保存成功！');
        this.reloadDatabaseObject();
      }
    }, error => {
      const head = '保存失败。';
      const errorString = <string>(error.text ? error.text() : error.text);
      this.handleErrorString(head, errorString);
    });
  }

  /**
 * 显示异常堆栈
 */
  showErrorStack(errorHead: string, errorMessage: string) {
    this.errorStackHead = errorHead;
    this.errorStack = errorMessage;
    this.showErrorStackWindow = !this.showErrorStackWindow;
  }
  /**
     * 异常处理
     * @param head
     * @param errorString
     */
  handleErrorString(head: string, errorString: string) {
    if (!isNullOrUndefined(errorString) && errorString.includes(this.errorToken)) {
      const strings = errorString.split(this.errorToken);
      const errorMessage = strings[1];
      this.showErrorStack(head, errorMessage);
    } else {
      this.showErrorStack(head, errorString);
    }
  }
  reloadDatabaseObject() {
    this.databaseObjectService.LoadDBO(this.fileUri).subscribe(result => {
      this.databaseObject = result;
      this.currentView = new DatabaseObjectTableView(result);
    });
  }
  addColumn() {
    this.columnCollection.addColumn();
  }
  deleteColumn() {
    this.columnCollection.deleteColumn();
  }
  tabSelectChange(args: any) {
    const { title } = args;
    this.currentTabTitle = title;
  }
}



