import { MapBase } from '../../base/mapbase';
import { List, Map, fromJS } from 'immutable';
import { isNullOrUndefined, isNull } from 'util';
import { DatabaseObjectIndex } from './databaseobjectindex';
import { DatabaseObjectColumnViewList } from './databaseobjectcolumnviewlist';
import { DatabaseObjectConst } from '../../const/databaseobjectconst';

export class DatabaseObjectIndexView extends MapBase {
    /**
    * 构造函数
    * @param element
    */
    constructor(element: DatabaseObjectIndex | Map<string, any>) {
        super(element);
        if (isNullOrUndefined(element)) {
            return;
        }
        this.data = this.getCurrentViewObject(this.data);
    }
    /**
 * 获取对象的Map
 * @param bizObject
 */
    private getCurrentViewObject(indexView: Map<string, any>): Map<string, any> {
        if (isNullOrUndefined(indexView)) {
            return;
        }
        let vo = indexView;
        vo = vo.update(DatabaseObjectConst.Columns, columns => this.getDatabaseObjectColumnViewList(columns));
        return vo;
    }

    private getDatabaseObjectColumnViewList(bizElements: List<any>): DatabaseObjectColumnViewList {
        const elementList = new DatabaseObjectColumnViewList(bizElements);
        return elementList;
    }
    get columns(): DatabaseObjectColumnViewList {
        return this.get(DatabaseObjectConst.Columns);
    }

    set columns(value: DatabaseObjectColumnViewList) {
        this.set(DatabaseObjectConst.Columns, value);
    }
}



