import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { catchError, map, tap } from 'rxjs/operators';
import { Response, Headers, RequestOptions, URLSearchParams, Http } from '@angular/http';
import {DatabaseObjectTable} from './utils/lib/DatabaseObjectTable';
import { Subject } from 'rxjs/Subject';
import { environment } from '../../../../environments/environment';
import { DatabaseObjectDto} from './utils/lib/DatabaseObjectDto';
import { itemAt } from '@progress/kendo-angular-grid/dist/es2015/data/data.iterators';
import { isNullOrUndefined } from 'util';

@Injectable()

export class DatabaseObjectService {
  private Server_IP: string;
  private DBO_Base_Path = '/api/dev/main/v1.0/database-objects';

  constructor(private http: Http) {
    this.Server_IP = environment.SERVER_IP;
   }
  private databaseObjectUrl = '';

  createDatabaseObject(relativePath: string, fileName: string, table: DatabaseObjectTable): Observable<any> {
   // this.refactorDatabaseObject(relativePath, fileName, table);
    // const dto = new DatabaseObjectDto(relativePath, fileName, 'Table', JSON.stringify(table));
    const url2 = this.Server_IP + this.DBO_Base_Path;
    return this.http.post(url2, this.getDto(relativePath, fileName, table), this.getOptions());
  }
  getDto(relativePath: string, fileName: string, table: DatabaseObjectTable) {
    table = this.buildMulitiLanguageColumns(table);
    table = this.buildPrimaryKey(table);
    const dto = new DatabaseObjectDto(relativePath, fileName, 'Table', JSON.stringify(table));
    return dto;
  }
  getOptions() {
    const headers = new Headers();
    headers.append('Content-Type', 'application/json');
    headers.append('Authorization', 'as');
    headers.append('Access-Control-Allow-Origin', '*');
    const options = new RequestOptions({
      params: {},
      headers: headers
    });
    return options;
  }

  saveDatabaseObject(relativePath: string, fileName: string, table: DatabaseObjectTable): Observable<any> {
   /* table = this.buildMulitiLanguageColumns(table);
    table = this.buildPrimaryKey(table);
    const dto = new DatabaseObjectDto(relativePath, fileName, 'Table', JSON.stringify(table));

    const headers = new Headers();
    headers.append('Content-Type', 'application/json');
    headers.append('Authorization', 'as');
    headers.append('Access-Control-Allow-Origin', '*');
    const options = new RequestOptions({
      params: {},
      headers: headers
    }); */
    const url2 = this.Server_IP + this.DBO_Base_Path;
    return this.http.put(url2, this.getDto(relativePath, fileName, table), this.getOptions());
  }

  public LoadDBO(url: string): Observable<any> {
    const dboFullPath = url.replace(/\\/g, '/') ;
    const dboUrl = this.Server_IP + this.DBO_Base_Path + dboFullPath;
    return this.http.get(dboUrl).pipe(
      map((res: any) => res.json())
    );
}

  buildMulitiLanguageColumns(table: DatabaseObjectTable) {
    if (isNullOrUndefined(table.multiLanguageColumns)) {
      table.multiLanguageColumns = new Array<string>();
      this.pushMulitiLanguageColumns(table);
    } else {
      this.pushMulitiLanguageColumns(table);
    }
    return table;
  }
  pushMulitiLanguageColumns(table: DatabaseObjectTable) {
    table.columns.forEach(column => {
      if (column.isMultiLanguage && table.multiLanguageColumns.find(item => item === column.id) == null) {
        table.multiLanguageColumns.push(column.id);
      }
    });
  }


  buildPrimaryKey(table: DatabaseObjectTable) {
    if (isNullOrUndefined(table.primaryKey)) {
      table.primaryKey = new Array<string>();
      this.pushPrimaryKey(table);
    } else {
      this.pushPrimaryKey(table);
    }
    return table;
  }
  pushPrimaryKey(table: DatabaseObjectTable) {
    table.columns.forEach(column => {
      if (column.ifPrimaryKey && table.primaryKey.find(item => item === column.id) == null) {
        table.primaryKey.push(column.id);
    }});
  }
}



