import { Injector, Injectable } from '@angular/core';
import { List, Map, fromJS, is } from 'immutable';
import { isNullOrUndefined } from 'util';
import { Subject } from 'rxjs/Subject';
import { Subscription } from 'rxjs/Subscription';
import { MapBase } from './mapbase';

export class ListBase<T extends MapBase>  {

    protected data: List<T>;

    private currentSelectIndex: number;

    /**
     * 选中新节点
     */
    public selectNewItem: Subject<T> = new Subject<T>();

    /**
     * 视图层刷新事件
     */
    public refreshView: Subject<this> = new Subject<this>();

    private selectNewSubscriptions: Subscription[];

    equals(value: ListBase<T>): boolean {
        return is(this.data, value.data);
    }

    /**
     * 构造函数
     */
    constructor(value: List<any> | any) {
        if (isNullOrUndefined(value)) {
            return;
        } else {
            if (List.isList(value)) {
                this.data = this.getListOfMapBase(value) as List<any>;
            } else {
                this.data = this.getListOfMapBase(List(value)) as List<any>;
            }
        }
        this.selectNewSubscriptions = new Array<Subscription>();
    }

    getListOfMapBase(values: List<any>): List<MapBase> {
        const list = values;
        for (let i = 0; i < values.size; i++) {
            list.update(i, value => new MapBase(values[i]));
        }
        return list;
    }

    setSelectIndex(index: string | number) {
        // if (this.currentSelectIndex === index && index != 0) {
        // 万一是由于删除引起的刷新，则虽然选中的索引不变，但是仍然需要刷新。
        // 本判断可避免多次重复刷新
        // 但是由于number类型初始值为0，所以无法避免第一行的重复选中导致的刷新
        // return;
        // }
        this.unsubscribeSelectNewItem();
        this.currentSelectIndex = +index;
        this.selectNewItem.next(this.get(this.currentSelectIndex));
        this.subscribeSelectNewItem(this.currentSelectItem);
    }

    setSelectIndexByID(id: string) {
        const index = this.getIndexByID(id);
        this.setSelectIndex(index);
    }

    getItemByID(id: string): MapBase {
        const list = this.data;
        for (let i = 0; i < list.size; i++) {
            const column = list.get(i) as MapBase;
            const currentID = column.get('id');
            if (id === currentID) {
                const subscription = column.refreshView.subscribe(item => {
                    this.refreshViewLayer();
                });

                return column;
            }
        }
        return null;
    }
    getI18nItem(): Array<MapBase> {
        const items = new Array<MapBase>();
        const list = this.data;
        for (let i = 0; i < list.size; i++) {
            const column = list.get(i) as MapBase;
            const isMultiLanguage = column.get('isMultiLanguage');
            if (isMultiLanguage) {
                const subscription = column.refreshView.subscribe(item => {
                    this.refreshViewLayer();
                });
                items.push(column);
            }
        }
        return items;
    }
    /**
     * 刷新视图层
     */
    protected refreshViewLayer() {

        this.refreshView.next(this);
    }

    /**
     * 查
     * @param index
     */
    get(index: number | string) {
        if (isNullOrUndefined(this.data)) {
            return;
        }
        return this.data.get(+index);
    }

    /**
     * 改
     * @param index
     * @param updater
     */
    update(index: number, updater: any) {
        if (isNullOrUndefined(this.data)) {
            return;
        }
        this.data = this.data.update(index, updater);
        this.refreshViewLayer();
    }

    set(index: number, newValue: any) {
        if (isNullOrUndefined(this.data)) {
            return;
        }
        this.data = this.data.set(index, newValue);
        this.refreshViewLayer();
    }

    /**
     * 删
     * @param index
     */
    remove(index: number | string) {
        if (isNullOrUndefined(this.data)) {
            return;
        }
        this.data = this.data.delete(+index);
        this.refreshViewLayer();
    }

    /**
     * 增在最前
     * @param value
     */
    unshift(value: T) {
        if (!this.data) {
            this.data = List();
        }
        this.data = this.data.unshift(value);
        this.refreshViewLayer();
    }

    /**
     * 增在最后
     * @param value
     */
    add(value: T) {
        if (!this.data) {
            this.data = List();
        }
        this.data = this.data.push(value);
        this.refreshViewLayer();
    }

    merge(values: this) {
        if (!this.data) {
            this.data = List();
        }
        this.data = this.data.merge(values.data);
        this.refreshViewLayer();
    }

    toJS(): Array<any> {
        const list = this.data;
        if (isNullOrUndefined(list)) {
            return;
        }

        const array = list.toJS();
        return array;
    }

    clear() {
        if (isNullOrUndefined(this.data)) {
            return;
        }
        this.data = List();
        this.refreshViewLayer();
    }

    get length(): number {
        if (isNullOrUndefined(this.data)) {
            return;
        }
        return this.data.size;
    }

    contains(item: T): boolean {
        return this.data.contains(item);
    }

    getIndexByID(id: string): number {
        if (isNullOrUndefined(this.data)) {
            return -1;
        }
        const list = this.data;
        for (let i = 0; i < list.size; i++) {
            const column = list.get(i) as MapBase;
            const currentID = column.get('id');
            if (id === currentID) {
                return i;
            }
        }
        return -1;
    }

    removeByID(id: string) {
        const index = this.getIndexByID(id);
        if (index === -1) {
            // alert('当前没有选中项。');
            return;
        }
        this.remove(index);
    }

    updateByID(id: string, updater: any) {
        const index = this.getIndexByID(id);
        if (index === -1) {
            // alert('当前没有选中项。');
            return;
        }
        this.update(index, updater);
    }

    get currentSelectItem(): T {

        if (isNullOrUndefined(this.currentSelectIndex)) {
            return null;
        } else {
            const item = this.get(this.currentSelectIndex);
            if (isNullOrUndefined(item)) {
                return null;
            }
            return item;
        }
    }

    subscribeSelectNewItem(item: T) {
        if (isNullOrUndefined(item)) {
            return;
        }

        const selectNewItemSubscription = item.refreshView.subscribe(value => {
            this.refreshViewLayer();
        });

        if (isNullOrUndefined(this.selectNewSubscriptions)) {
            this.selectNewSubscriptions = new Array<Subscription>();
        }
        this.selectNewSubscriptions.push(selectNewItemSubscription);
    }

    unsubscribeSelectNewItem() {

        if (isNullOrUndefined(this.selectNewSubscriptions) || this.selectNewSubscriptions.length === 0) {
            return;
        } else {
            while (this.selectNewSubscriptions.length > 0) {
                const subscription = this.selectNewSubscriptions.pop();
                subscription.unsubscribe();
            }
        }
    }

    // /**
    //  * 数据重置
    //  * @param newValue
    //  */
    // reset(newValue: this) {
    //     this.data = newValue.data;
    //     this.refreshViewLayer();
    // }
}



