import { DatabaseObjectColumnViewList } from './databaseobjectcolumnviewlist';
import { DatabaseObjectTable } from './databaseobjecttable';
import { MapBase } from '../../base/mapbase';
import { DatabaseObjectConst} from '../../const/databaseobjectconst';
import { isNullOrUndefined } from 'util';
import { Subject } from 'rxjs/Subject';
import { List, Map, fromJS } from 'immutable';
import { DatabaseObjectIndexViewList} from './databaseobjectindexviewlist';

export class DatabaseObjectTableView extends MapBase {

    /**
     * 业务对象名刷新事件
     */
    refreshName: Subject<boolean> = new Subject<boolean>();

    /**
     * 构造函数
     * @param object
     */
    constructor(object: DatabaseObjectTable | Map<string, any>) {
        super(object);
        if (isNullOrUndefined(object)) {
            return;
        }
        this.data = this.getCurrentViewObject(this.data);
    }

    /**
     * 获取对象的Map
     * @param bizObject
     */
    private getCurrentViewObject(databseObject: Map<string, any>): Map<string, any> {
        if (isNullOrUndefined(databseObject)) {
            return;
        }
        let vo = databseObject;
        vo = vo.update(DatabaseObjectConst.Columns, columns => this.getDatabaseObjectColumnViewList(columns));
        vo = vo.update(DatabaseObjectConst.Indexes, indeses => this.getDatabaseObjectIndexViewList(indeses));
        return vo;
    }

    private getDatabaseObjectColumnViewList(bizElements: List<any>): DatabaseObjectColumnViewList {
        const elementList = new DatabaseObjectColumnViewList(bizElements);
        return elementList;
    }
    private getDatabaseObjectIndexViewList(bizElements: List<any>): DatabaseObjectIndexViewList {
        const elementList = new DatabaseObjectIndexViewList(bizElements);
        return elementList;
    }
    get columns(): DatabaseObjectColumnViewList {
        return this.get(DatabaseObjectConst.Columns);
    }

    set columns(value: DatabaseObjectColumnViewList) {
        this.set(DatabaseObjectConst.Columns, value);
        this.refreshName.next(true);
    }
    get indexes(): DatabaseObjectIndexViewList {
        return this.get(DatabaseObjectConst.Indexes);
    }

    set indexes(value: DatabaseObjectIndexViewList) {
        this.set(DatabaseObjectConst.Indexes, value);
        this.refreshName.next(true);
    }
    get ID() {
        return this.get(DatabaseObjectConst.ID);
    }


    get Description(): string {
        return this.get(DatabaseObjectConst.Description);
    }

    set Description(description: string) {
        this.set(DatabaseObjectConst.Description, description);
        this.refreshName.next(true);
    }

    get Name(): string {
        return this.get(DatabaseObjectConst.Name);
    }

    get IsI18nObject(): boolean {
        return this.get(DatabaseObjectConst.IsI18nObject);
    }

    set IsI18nObject(isI18nObject: boolean) {
        this.set(DatabaseObjectConst.IsI18nObject, isI18nObject);
        this.refreshName.next(true);
    }

    get IsFiscalTable(): boolean {
        return this.get(DatabaseObjectConst.IsFiscalTable);
    }

    set IsFiscalTable(isFiscalTable: boolean) {
        this.set(DatabaseObjectConst.IsFiscalTable, isFiscalTable);
        this.refreshName.next(true);
    }
    get IsUsingTimeStamp(): boolean {
        return this.get(DatabaseObjectConst.IsFiscalTable);
    }

    set IsUsingTimeStamp(isUsingTimeStamp: boolean) {
        this.set(DatabaseObjectConst.IsUsingTimeStamp, isUsingTimeStamp);
        this.refreshName.next(true);
    }
    get IsSynHis(): boolean {
        return this.get(DatabaseObjectConst.IsSynHis);
    }

    set IsSynHis(isSynHis: boolean) {
        this.set(DatabaseObjectConst.IsSynHis, isSynHis);
        this.refreshName.next(true);
    }
    get Code(): string {
        return this.get(DatabaseObjectConst.Code);
    }

    set Code(code: string) {
        this.set(DatabaseObjectConst.Code, code);
        this.refreshName.next(true);
    }

    set Name(name: string) {
        this.set(DatabaseObjectConst.Name, name);
        this.refreshName.next(true);
    }

    get PrimaryKey(): string[] {
        return this.get(DatabaseObjectConst.PrimaryKey);
    }

    set PrimaryKey(primaryKey: string[]) {
        this.set(DatabaseObjectConst.PrimaryKey, primaryKey);
        this.refreshName.next(true);
    }
}



