import { BaseConst } from './baseconst';

export class DatabaseObjectColumnConst extends BaseConst {
    // 字段
    static IsMultiLanguage = 'isMultiLanguage';
    static DefaultValue = 'defaultValue';

    // 数据类型
    static DataType = 'dataType';
    static Length = 'length';
    static Precision = 'precision';
    static Scale = 'scale';

    static IsNullable = 'isNullable';
    static IsUnique = 'isUnique';
    static IfPrimaryKey = 'ifPrimaryKey';
}



