import {DatabaseObjectColumn} from './databaseobjectcolumn';
import {DatabaseObjectIndex} from './databaseobjectindex';
import { Guid } from './guid';

export class DatabaseObjectTable {
    constructor(code: string, name: string) {
      this.id = Guid.newGuid();
      this.code = code;
      this.name = name;
      this.type = 0;
      this.columns = [];
      this.indexes = [];
      this.multiLanguageColumns = [];
      this.isI18nObject = false;
      this.isFiscalTable = false;
      this.isUsingTimeStamp = false;
      this.isSynHis = false;
      this.description = '';
      this.primaryKey = [];
    }
    id: string;
    code: string;
    name: string;
    description: string;
    columns: DatabaseObjectColumn[];
    indexes: DatabaseObjectIndex[];
    isI18nObject: boolean;
    multiLanguageColumns: string[];
    isFiscalTable: boolean;
    isUsingTimeStamp: boolean;
    isSynHis: boolean;
    type: number;
    primaryKey: string[];
}



