import { Injector, Injectable } from '@angular/core';
import { List, Map, fromJS } from 'immutable';
import { isNullOrUndefined } from 'util';
import { Subject } from 'rxjs/Subject';
import { ListBase } from '../../base/listbase';
import { DatabaseObjectColumn} from './databaseobjectcolumn';
import { DatabaseObjectColumnView } from './databaseobjectcolumnview';

export class DatabaseObjectColumnViewList extends ListBase<DatabaseObjectColumnView> {

    constructor(elements: DatabaseObjectColumn[] | List<any>) {
        super(elements);
        if (elements) {
            this.data = this.getElements(this.data);
        }
    }

    /**
     * 构造时获取当前对象的字段集合列表的List
     * @param elementArray
     */
    private getElements(elementArray: List<any>): List<any> {
        if (isNullOrUndefined(elementArray)) {
            return;
        }

        let databaseObjectColumnList = elementArray;

        if (elementArray.size > 0) {
            for (let i = 0; i < elementArray.size; i++) {
                databaseObjectColumnList = databaseObjectColumnList.update(i, obj => this.getViewElement(elementArray.get(i)));
            }
            return databaseObjectColumnList;
        }
    }

    /**
     * 构造时获取字段的Map
     * @param element
     */
    protected getViewElement(element: Map<string, any>): any {
        const viewEle = new DatabaseObjectColumnView(element);
        return viewEle;
    }
}



