import { MapBase } from '../../base/mapbase';
import { List, Map, fromJS } from 'immutable';
import { isNullOrUndefined, isNull } from 'util';
import { DatabaseObjectColumnConst } from '../../const/databaseobjectcolumnconst';
import { Guid } from './guid';
import {DataType} from './datatype';

export class DatabaseObjectColumn {

    constructor() {
        this.id = Guid.newGuid();
        this.length = 36;
        this.precision = 0;
        this.dataType = 1;
        this.isNullable = true;
        this.ifPrimaryKey = false;
        this.scale = 0;
        this.defaultValue = '';
        this.isMultiLanguage = false;
     }
    id: string;
    code: string;
    name: string;
    dhowName: string;
    description: string;
    dataType: number;
    length: number;
    precision: number;
    scale: number;
    defaultValue: string;
    isNullable: boolean;
    isUnique: boolean;
    ifPrimaryKey: boolean;
    isMultiLanguage: boolean;
}



