import { Guid } from './guid';

export class DatabaseObjectSPVF {
    constructor(code: string, name: string, type: number) {
      this.id = Guid.newGuid();
      this.code = code;
      this.name = name;
      this.type = 0;
    }
     id: string;
    code: string;
    name: string;
    type: number;
    description: string;
}



