import { Component, OnInit, ViewChild, Input, OnChanges, SimpleChanges} from '@angular/core';
import { GridComponent } from '@progress/kendo-angular-grid';
import { TableIndexColumnEditComponent} from '../table-index-column-edit/table-index-column-edit.component';
import { DatabaseObjectIndexView } from '../utils/lib/databaseobjectindexview';
import { isNullOrUndefined, isNull } from 'util';
import { DatabaseObjectColumn } from '../utils/lib/databaseobjectcolumn';
import { DatabaseObjectIndex } from '../utils/lib/databaseobjectindex';
import { DatabaseObjectTableView } from '../utils/lib/databaseobjecttableview';
import { RequestElement } from '../utils/lib/requestelement';
import { DatabaseObjectTable } from '../utils/lib/databaseobjecttable';
import { DatabaseObjectIndexConst } from '../const/databaseobjectindexconst';
import {DatabaseObjectIndexViewList} from '../utils/lib/databaseobjectindexviewlist';
@Component({
  selector: 'app-table-index-column-grid',
  templateUrl: './table-index-column-grid.component.html',
  styleUrls: ['./table-index-column-grid.component.css']
})
export class TableIndexColumnGridComponent implements OnInit, OnChanges {
  @ViewChild('grid') grid: GridComponent;
  @ViewChild('indexColumnsEdit') editElements: TableIndexColumnEditComponent;

  @Input() currentView: DatabaseObjectTableView;
  @Input() currentIndexView: DatabaseObjectIndexView;
  currentIndex: DatabaseObjectIndex;
  indexColumns: DatabaseObjectColumn[];
  table: DatabaseObjectTable;
  subscriptions = new Array<any>();
  constructor() { }

  ngOnInit() {
    this.refresh();
  }
  ngOnChanges(changes: SimpleChanges): void {
    if (changes['currentView']) {
      this.currentView = changes['currentView'].currentValue;
      this.table = this.currentView.toJS();
    }
    if (changes['currentIndexView']) {
      this.currentIndexView = changes['currentIndexView'].currentValue;
      if (!isNullOrUndefined(this.currentIndexView)) {
        this.currentIndex = this.currentIndexView.toJS();
      }
    }
    this.refresh();
  }
  private refresh() {
    if (isNullOrUndefined(this.currentIndexView) || isNullOrUndefined(this.currentIndexView.columns)
    || this.currentIndexView.columns.length <= 0) {
      this.clearAllData();
      return;
    } else {
      this.indexColumns = this.currentIndexView.columns.toJS();
    }
  }

  clearAllData() {
    this.indexColumns = null;
  }
  closeGridCell() {
    // 关闭所有正在编辑中的单元格
    this.grid.closeCell();
    // 取消所有编辑中的列
    this.grid.cancelCell();
    // 取消新增行
    this.grid.closeRow(-1);
  }

  saveHandler(requestKeyValuePair: RequestElement) {
    if (isNullOrUndefined(requestKeyValuePair)) {
      this.refresh();
      return;
    }
    const selectedIndexColumn = [];
    if (!isNullOrUndefined(requestKeyValuePair.RequestElementValue) && requestKeyValuePair.RequestElementValue.length > 0) {
      requestKeyValuePair.RequestElementValue.forEach(item => {
         const column = this.currentView.columns.getItemByID(item).toJS();
        selectedIndexColumn.push(column);
      });
    }
    const columns = new DatabaseObjectIndexViewList(selectedIndexColumn);
    this.updateValueByElementId(this.currentIndex.id, DatabaseObjectIndexConst.Columns, columns);
    this.currentIndexView.update(DatabaseObjectIndexConst.Columns, oldValue => columns);
    this.refresh();
  }
  /**
* 根据字段ID更新属性值
* @param elementId 字段ID
* @param propertyName 属性名
* @param propertyValue 属性值
*/
  updateValueByElementId(elementId: string, propertyName: string, propertyValue: any) {
    this.currentView.indexes.updateByID(elementId, elementView => {
      elementView.update(propertyName, oldValue => propertyValue);
      return elementView;
    });
  }

  cancelHandler(args: any) {

  }
  editIndexColumns() {
    this.editElements.load(this.currentIndex, this.indexColumns, this.table);
  }
  /**
       * 订阅视图刷新事件
       */
  private subscribeRefreshView() {
    this.unsubscribeRefreshView();
    if (isNullOrUndefined(this.currentView.indexes)) {
      return;
    }
    // 订阅当前关联数据源的值刷新事件
    // 每次刷新后，需重新绑定字段列表的事件
    const refreshViewSubscription = this.currentView.indexes.refreshView.subscribe(
      value => {
        if (isNullOrUndefined(this.currentView)) {
          this.indexColumns = null;
          this.currentIndexView = null;
          return;
        }
        if (isNullOrUndefined(this.currentIndexView)) {
          this.indexColumns = null;
          this.currentIndexView = null;
        } else if ( isNullOrUndefined(this.currentIndexView.columns)
        || this.currentIndexView.columns.length <= 0) {
          this.indexColumns = null;
        } else {
          this.indexColumns = this.currentIndexView.columns.toJS();
        }
      }
    );
    this.subscriptions.push(refreshViewSubscription);
  }

  /**
   * 取消订阅视图刷新事件
   */
  private unsubscribeRefreshView() {
    while (this.subscriptions.length > 0) {
      const subscription = this.subscriptions.pop();
      subscription.unsubscribe();
    }
  }
}



