import { Component, OnInit, OnChanges, EventEmitter, Input, Output, SimpleChanges } from '@angular/core';
import { DatabaseObjectTable } from '../utils/lib/DatabaseObjectTable';
import { DatabaseObjectService } from '../database-object.service';
import { DatabaseObjectColumn } from '../utils/lib/databaseobjectcolumn';
import { DatabaseObjectTableView } from '../utils/lib/databaseobjecttableview';
import {DatabaseObjectIndex} from '../utils/lib/databaseobjectindex';
import { SelectableSettings } from '@progress/kendo-angular-grid';
import { isNullOrUndefined, isNull } from 'util';
import { RequestElement } from '../utils/lib/requestelement';
import { Observable } from 'rxjs/Observable';
import { of } from 'rxjs/observable/of';
import { DatabaseObjectColumnConst } from '../const/databaseobjectcolumnconst';

@Component({
  selector: 'app-table-index-column-edit',
  templateUrl: './table-index-column-edit.component.html',
  styleUrls: ['./table-index-column-edit.component.css']
})
export class TableIndexColumnEditComponent implements OnInit, OnChanges {
  @Input() currentView: DatabaseObjectTableView;
  @Input() currentIndex: DatabaseObjectIndex;

  active = false;
  selectedKeys: string[] = [];
  columns: DatabaseObjectColumn[];

  @Output() cancel: EventEmitter<any> = new EventEmitter();
  @Output() save: EventEmitter<any> = new EventEmitter();
  /**
   * 允许多选
   */
  private selectableSettings: SelectableSettings = {
    mode: 'multiple'
  };

  /**
   * 当前引用字段id集合，设置为选中
   */
  private eleSelection: string[] = [];
  constructor() { }

  ngOnInit() {
    if (!isNullOrUndefined(this.currentView) && !isNullOrUndefined(this.currentView.columns)) {
      this.columns = this.currentView.columns.toJS();
    }
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes['currentView']) {
      this.currentView = changes['currentView'].currentValue;
      this.columns = this.currentView.columns.toJS();
    }
    if (changes['currentIndex']) {
      this.currentIndex = changes['currentIndex'].currentValue;
    }
  }
  /**
   * 加载
   * @param refViewElements 关联字段集合
   * @param refModel 关联模型
   */
  load(index: DatabaseObjectIndex, columns: DatabaseObjectColumn[], currentObject: DatabaseObjectTable) {
    this.active = true;
    // 设置当前选中对象
    this.currentIndex = index ? index : this.currentIndex;
    if (!isNullOrUndefined(this.currentIndex)) {
      this.selectedKeys = [this.currentIndex.id];
    }

    this.getObjectElements(currentObject);
    this.getSelectElements(columns);
  }

  /**
   * 获取当前引用字段集合
   * @param refModel 关联模型
   */
  private getObjectElements(currentObject: DatabaseObjectTable): any {
    if (isNullOrUndefined(currentObject) || isNullOrUndefined(currentObject.columns)) {
      this.columns = [];
      return;
    }
    this.columns = currentObject.columns;
  }

  /**
   * 根据当前引用字段集合获取选中状态
   */
  private getSelectElements(columns: DatabaseObjectColumn[]): any {
    this.eleSelection = [];
    if (!isNullOrUndefined(columns)) {
      columns.forEach(item => {
        if (isNullOrUndefined(this.eleSelection.find(selection => selection === item.id))) {
           this.eleSelection.push(item.id);
        }
      });
    }
  }


  public onConfirm(e): void {
    e.preventDefault();
    const requestElement = new RequestElement();
    requestElement.RequestElementKey = this.currentIndex.id;
    // 若选中子节点，则记录当前子节点ID，否则key为空
    if (this.eleSelection.length === 0) {
      this.save.emit(null);
    } else {
      requestElement.RequestElementValue = this.eleSelection;
      this.save.emit(requestElement);
    }
    this.closeForm();
  }

  public onCancel(e): void {
    e.preventDefault();
    this.closeForm();
  }

  private closeForm(): void {
    this.active = false;
  }

   /**
   * 捕捉选择项改变事件,获取当前选中节点的索引
   * @param param0
   */
  // public handleSelection(args: any): void {
  //   const dataItem = args;
  //   if (dataItem.selected) {
  //     const currentColumn = this.columns[dataItem.index];
  //     dataItem.deselectedRows = [];
  //     if (!isNullOrUndefined(this.eleSelection.find(item => item === currentColumn.id))) {
  //       const s = {'dataItem': currentColumn};
  //       dataItem.deselectedRows.push(s);
  //     }
  //   }
  // }
}



