import { Injector, Injectable } from '@angular/core';
import { List, Map, fromJS } from 'immutable';
import { isNullOrUndefined } from 'util';
import { Subject } from 'rxjs/Subject';


export class MapBase {


    protected data: Map<any, any>;

    /**
     * 构造函数
     */
    constructor(value: any) {
        if (isNullOrUndefined(value)) {
            // this.data = Map();
            return;
        } else {
            // this.data = Map(value);

            if (Map.isMap(value)) {
                this.data = value;
            } else {
                this.data = Map(value);
            }

        }
    }

    /**
     * 视图层刷新事件
     */
    refreshView: Subject<this> = new Subject<this>();

    /**
     * 刷新视图层
     */
    public refreshViewLayer() {
        this.refreshView.next(this);
    }

    /**
     * 查
     * @param path
     */
    get(path: string) {
        if (isNullOrUndefined(this.data)) {
            return;
        }
        return this.data.get(path);
    }

    getIn(path: string[]) {
        if (isNullOrUndefined(this.data)) {
            return;
        }
        return this.data.getIn(path);
    }

    set(path: string, newValue: any) {
        if (isNullOrUndefined(this.data)) {
            return;
        }
        this.data = this.data.set(path, newValue);
        this.refreshViewLayer();
    }
    /**
     * 改
     * @param path
     * @param updater value=>fx(value)形式
     */
    update(path: string, updater: any) {
        // if (path.length > 0) {
        //     this.data = this.data.updateIn(path as string[], newValue);
        // }
        // else {
        // this.data = this.data.update(path, newValue);
        // }
        if (isNullOrUndefined(this.data)) {
            return;
        }
        this.data = this.data.update(path, updater);
        this.refreshViewLayer();
    }

    toJS() {
        if (isNullOrUndefined(this.data)) {
            return;
        }
        return this.data.toJS();
    }

    // /**
    //  * 数据重置
    //  * @param newValue
    //  */
    // reset(newValue: this) {
    //     this.data = newValue.data;
    //     this.refreshViewLayer();
    // }
}



