import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { TableDesignerComponent } from './table-designer/table-designer.component';
import { ColumnsManagerComponent } from './columns-manager/columns-manager.component';
import { IndexesManagerComponent } from './indexes-manager/indexes-manager.component';
import { DatabaseObjectService } from './database-object.service';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpClient, HttpClientModule } from '@angular/common/http';
import { GridModule } from '@progress/kendo-angular-grid';
import { InputsModule } from '@progress/kendo-angular-inputs';
import { TreeViewModule } from '@progress/kendo-angular-treeview';
import { GSPDBODataTypeConvert} from './columns-manager/ColumnsUtils';
import { DropDownsModule, DropDownListModule } from '@progress/kendo-angular-dropdowns';
import { LayoutModule, SplitterModule } from '@progress/kendo-angular-layout';
import { DialogModule } from '@progress/kendo-angular-dialog';
import { ButtonModule, ButtonsModule, ButtonGroupModule } from '@progress/kendo-angular-buttons';
import { PopupModule } from '@progress/kendo-angular-popup';
import { DataBindingDirective, GridComponent, SelectableSettings } from '@progress/kendo-angular-grid';
import { BasicInfoCardComponent } from './basic-info-card/basic-info-card.component';
import { TableIndexColumnGridComponent } from './table-index-column-grid/table-index-column-grid.component';
import { TableIndexColumnEditComponent } from './table-index-column-edit/table-index-column-edit.component';
import {ErrorStackWindowComponent} from './error-stack-window/error-stack-window.component';
// import { MessagerModule } from '@farris/ui';

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    ButtonsModule,
    GridModule,
    SplitterModule,
    DialogModule,
    PopupModule,
    DropDownsModule,
    InputsModule,
    ButtonGroupModule,
    ButtonModule,
    LayoutModule,
    TreeViewModule,
    HttpClientModule,
    DropDownListModule,
    // MessagerModule.forRoot(),
    ReactiveFormsModule
  ],
  exports: [TableDesignerComponent, ErrorStackWindowComponent],
  providers: [TableDesignerComponent, DatabaseObjectService, HttpClient],
  entryComponents: [TableDesignerComponent],
  declarations: [
    TableDesignerComponent,
    ColumnsManagerComponent,
    GSPDBODataTypeConvert,
    IndexesManagerComponent,
    BasicInfoCardComponent,
    TableIndexColumnGridComponent,
    TableIndexColumnEditComponent,
    ErrorStackWindowComponent
  ]
})
export class TableDesignerModule { }



