import { Component, ViewChild, OnInit, EventEmitter, TemplateRef, Output } from '@angular/core';
import { Observable, Subject } from 'rxjs';
import { DboType, DboTypes, } from '../dbotypes';
import {DatabaseObjectTable} from '../../tabledesigner-frame/view/tabledesigner-frame/utils/lib/databaseobjecttable';
import {DatabaseObjectColumn} from '../../tabledesigner-frame/view/tabledesigner-frame/utils/lib/databaseobjectcolumn';
import { DatabaseObjectSPVF } from '../../tabledesigner-frame/view/tabledesigner-frame/utils/lib/databaseobjectspvf';
import {DatabaseObjectService} from '../../tabledesigner-frame/view/tabledesigner-frame/database-object.service';
import { isNullOrUndefined } from 'util';
import { isNullOrEmptyString } from '@progress/kendo-angular-grid/dist/es2015/utils';

@Component({
  selector: 'app-dbo-creator',
  templateUrl: './dbo-creator.component.html',
  styleUrls: ['./dbo-creator.component.css']
})
export class DboCreatorComponent implements OnInit {
  @ViewChild('modalFooter') modalFooter: TemplateRef<any>;
  @Output() createDboEvent = new EventEmitter<any>();

  modalClosed: EventEmitter<any>;
  createDboSuccess: EventEmitter<any>;
  dboTypes= DboTypes;
  code: string;
  name: string;
  selectedType: number;
  path: string;
  createDbo: EventEmitter<any> = new EventEmitter();
  relativePath: string;
  public defaultItem: { index: number, text: string, value: DboType } = { index: 0, text: '表', value: DboType.Table };
 /**
 * 异常堆栈窗口是否显示
 */
  showErrorStackWindow = false;
  errorStack: string;
  errorStackHead: string;
  errorToken = '#GSPDBOError#';
  savedErrorToken = '#saved#';
  constructor(private service: DatabaseObjectService) {
    this.modalClosed = new EventEmitter<any>();
    this.createDboSuccess = new EventEmitter<any>();
  }

  ngOnInit() {
    // this.isProjectShown = false;
    this.relativePath = this.path.substring(1, this.path.lastIndexOf('/'));
    this.selectedType = 0;
  }

  /**
 * 关闭模态框
 */
  clickCloseModal() {
    this.modalClosed.emit();
  }
  okClick() {
    let dbo = null;
    if (isNullOrEmptyString(this.code)) {
      alert('编号不能为空');
      return;
    }
    if (isNullOrEmptyString(this.name)) {
      alert('名称不能为空');
      return;
    }
  if (this.selectedType === 0) {
    dbo = this.BuildNewTable();
  }else {
    dbo = new DatabaseObjectSPVF(this.code, this.name, this.selectedType);
  }
    this.service.createDatabaseObject(this.relativePath, '', dbo).subscribe(() => {
      alert('创建成功。');
      this.modalClosed.emit();
      this.createDboSuccess.emit(this.path);
      this.createDboEvent.emit(this.path + '/' + this.code + '.dbo');
    },  error => {
      const head = '创建失败。';
      const errorString = <string>(error.text ? error.text() : error.text);
      this.handleErrorString(head, errorString);
    });
  }

  /**
 * 显示异常堆栈
 */
  showErrorStack(errorHead: string, errorMessage: string) {
    this.errorStackHead = errorHead;
    this.errorStack = errorMessage;
    this.showErrorStackWindow = !this.showErrorStackWindow;
  }
  /**
     * 异常处理
     * @param head
     * @param errorString
     */
  handleErrorString(head: string, errorString: string) {
    if (!isNullOrUndefined(errorString) && errorString.includes(this.errorToken)) {
      const strings = errorString.split(this.errorToken);
      const errorMessage = strings[1];
      this.showErrorStack(head, errorMessage);
    } else {
      this.showErrorStack(head, errorString);
    }
  }



  /**
  * 返回值类型切换事件
  * @param selectedIndex 返回值类型集合中的索引
  */
  selectType({ value }: any) {
    this.selectedType = value;
  }
  BuildNewTable() {
    const table = new DatabaseObjectTable(this.code, this.name);
    const column = new DatabaseObjectColumn();
    column.code = 'id';
    column.name = 'id';
    column.isUnique = true;
    column.isNullable = false;
    column.ifPrimaryKey = true;
    table.columns.push(column);
    return table;
  }
}



